<template>
  <div class="responsible-gaming block">
    <table class="responsibleGamingTable">
      <thead class="responsibleGamingTable__thead">
      <tr class="">
        <td class="">Limit type</td>
        <td class="">Amount/period</td>
        <td class="">Status</td>
        <td class="">Expiration date</td>
      </tr>
      </thead>
      <tbody  class="responsibleGamingTable__tbody">
  
      <tr class="" v-for="item in items" :key="item.id">
        <td class="responsibleGamingTable__limit">{{ item.limit }}</td>
        <td class="">{{ item.period }}</td>
        <td class="responsibleGamingTable__status" :style="{color: colorStatus(item.status)}">{{ item.status }}</td>
        <td class="responsibleGamingTable__expiration">{{ item.expiration }}  <span class="responsibleGamingTable__editIcon"></span></td>
      </tr>
      </tbody>
    </table>
    
  </div>
</template>

<script>
 
	export default {
		name: "ResponsibleGaming",
		data() {
			return {
				items: [
					{dataType: 'type-1', limit: 'Deposit limit', period: '12  € / day', color: 1, status: 'Active', expiration: 'No expiration'},
					{dataType: 'type-1', limit: 'Wagering limit', period: 'No limit', color: 2, status: 'Cancelation pending', expiration: 'No expiration'},
					{dataType: 'type-2', limit: 'Session time ', period: '12  € / month', color: 3, status: 'Inactive', expiration: 'No expiration'},
					{dataType: 'type-1', limit: 'Loss limit', period: '12  € / day', color: 1, status: 'Active', expiration: 'No expiration'},
					{dataType: 'type-2', limit: 'Self-exclusion', period: 'No limit', color: 2, status: 'Cancelation pending', expiration: 'No expiration'},
					{dataType: 'type-2', limit: 'Cool off', period: '11 month', color: 3, status: 'Inactive', expiration: '03.08.19'}
				],
			}
		},

		methods: {
      colorStatus(status) {
        switch(status) {
          case 'Active'              : return '#86C339';
          case 'Cancelation pending' : return '#FFC000';
          case 'Inactive'            : return '#F13F35';
          default                    : return '#191919';
        }
      },
		},
	}
</script>

<style lang="sass" scoped>
  .responsible-gaming
    padding: 0
  
  .responsibleGamingTable
    width: 100%
    text-align: left
    border-collapse: collapse
    td
      padding: 15px 25px
    &__thead
      background: rgba(243, 244, 245, 0.2)
      td
        border: 1px solid #F3F4F5
    &__tbody
      td
        border-bottom: 1px solid #F3F4F5
      tr
        &:hover
          td
            background: rgba(243, 244, 245, 0.3)
          .responsibleGamingTable__editIcon
            display: block
    &__limit
      font-weight: 500
    &__status
      font-weight: 500
    &__expiration
      padding-right: 30px
      position: relative
      display: flex
      align-items: center
    &__editIcon
      position: absolute
      right: 20px
      cursor: pointer
      display: none
      width: 18px
      height: 20px
      background: url(../../../assets/img/account/marker.png) center center no-repeat
  
  

</style>

